:root {
    /* Black -> white shites*/
    --color-black: #000;
    --color-dark-gray: #767779;
    --color-gray: #B7B8BE;
    --color-light-gray: #D8D8D8;
    --color-lighter-gray: #EAEAEA;
    --color-white: #fff;

    /* Colours */
    --color-green: #D6FFEA;
    --color-red: #D0021B;
    --color-pink: #FFD7D7;
    --color-blue: #ADE1F3;

    /* Layout */
    --max-container-width: 1200px;

    /* Fonts */
    --font-family-default: 'Open Sans', sans-serif;

    /* Animations */
    --default-transition-time: 0.2s;
    --default-transition-mode: ease-in;
}

body {
    background: var(--color-white) url(/static/media/bg.61c38614.svg) no-repeat -223px 0;
    background-size: 90%;
    color: var(--color-black);
    font-family: var(--font-family-default);
    font-size: 16px;
    line-height: 26px;
}

img {
    max-width: 100%;
}

.page-title {
    text-shadow: 4px 4px 6px var(--color-green);
    font-size: 48px;
    font-style: italic;
    font-weight: 800;
    margin: 50px auto 70px;
    max-width: 300px;
    text-align: center;
    -webkit-transform: rotate(-8deg);
            transform: rotate(-8deg);
}

@media (min-width: 900px) {
    .page-title {
        margin: 66px auto 105px;
    }
}

.container {
    max-width: var(--max-container-width);
    margin: 0 auto;
    padding: 0 20px 20px;
}

@media (min-width: 1250px) {
    .container {
        padding: 0;
    }
}

button {
    background: var(--color-lighter-gray);
    border: 0;
    cursor: pointer;
    color: var(--color-dark-gray);
    display: inline-block;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 0.5px;
    padding: 22px 40px;
}

button.large {
    padding: 22px 30px;
    max-width: 100%;
    width: 100%;
}

button:disabled,
button.disabled {
    cursor: not-allowed;
}

button.primary {
    background: var(--color-pink);
    color: var(--color-black);
    transition: background-color var(--default-transition-time) var(--default-transition-mode);
}

button.primary:hover {
    background: var(--color-blue);
}

@media (min-width: 480px) {
    button.large {
        padding: 22px 81px;
        width: auto;
    }
}
